<template lang="pug">
    .group__schedule-day
        select(@change='onChange()' v-model="data.scheduleTime" :disabled='!editMode').existed__group-schedule-day
            option(:value='0') Выбрать 
            option(:value='data.scheduleTime' :selected='true').existed__group-schedule-option {{times[data.scheduleTime-1]}}
            option(:value='idx+1' v-for='item,idx in filteredTimes' :selected='data.scheduleTime==idx+1').existed__group-schedule-option {{item}}
</template>
<script>
import { mapState } from "vuex";

export default {
    data() {
        return {
            data: {
                id: 0,
                scheduleDay: this.day,
                scheduleTime: this.time
            },
            selected: 0,
            disabled: 0,
            times: [
                '08.00-09.00', '09.00-10.00', '10.00-11.00', '11.00-12.00', '12.00-13.00',
                '13.00-14.00', '14.00-15.00', '15.00-16.00', '16.00-17.00',
                '17.00-18.00'
            ]
        }
    },
    computed: {   
        ...mapState ("teacherSchedule", {
            teacherSchedule: state => state.teacherSchedule}),   
        filteredTimes:  function() {
            return this.times
            .filter((item, idx) => this.teacherSchedule.filter(ele=>ele.scheduleDay==this.day).length==0 || this.teacherSchedule.filter(ele=>ele.scheduleDay==this.day)[0].scheduleTime != idx+1)
        }
    },
    props: {
        editMode: Boolean,
        day: Number,
        time: Number,
        group: Object    },
    methods: {
        onChange(){
            this.data.id =(this.day - 1)*10 + this.data.scheduleTime
            this.$emit('editSchedule', this.data)
        }
    }
} 
</script>
<style scoped lang="scss">
.existed__group-schedule-day {
    background-color: #FFFAF0;
    border-radius: 5px;
    padding: 4px;
    width: 80%;
}
</style>